/* DayPicker styles */

.DayPicker {
    /*display: inline-block;*/
}

.Current-date-calendar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #7CB8B0;
    color: #fff;
    font-size: 14px;
    padding: 4px 10px 4px 15px;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
}

.DayPicker-wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    position: relative;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    flex-direction: row;
    outline: none;
}

.DayPicker-Month {
    display: table;
    border-collapse: separate;
    border-spacing: 10px 0;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    /*margin: 0 1rem;*/
    width: 100%
}

.DayPicker-NavBar {
    position: absolute;
    left: 0;
    right: 0;
    padding: 0 .5rem;
    top: 0.4rem;
}

.DayPicker-NavButton {
    position: absolute;
    width: 1.5rem;
    height: 1.5rem;
    background-size: contain;
    cursor: pointer;
}

.DayPicker-NavButton--prev {
    left: 1rem;
    background-size: 30%;
}

.DayPicker-NavButton--next {
    right: 1rem;
    background-size: 30%;
}

.DayPicker-NavButton--interactionDisabled {
    display: none;
}

.DayPicker-Caption {
    display: table-caption;
    height: 1.3rem;
    text-align: center;
    background-color: #fff;
    display: none;
    color: #fff;
    padding: 0.5rem;
}

.DayPicker-Weekdays {
    display: table-header-group;
}

.DayPicker-WeekdaysRow {
    display: table-row;
}

.DayPicker-Weekday {
    display: table-cell;
    font-size: 1em;
    padding: 0px;
    text-align: center;
    color: #8b9898;
    width: 30px;
}

.DayPicker-Weekday abbr{
    text-decoration: none;
}

.DayPicker-Body {
    display: table-row-group;
}

.DayPicker-Week {
    display: table-row;
}

.DayPicker-Day {
    display: table-cell;
    padding: 0px;
    height: 28px;
    margin: 5px;
    outline: none;
    /*border: 1px solid #eaecec;*/
    text-align: center;
    cursor: pointer;
    vertical-align: middle;
    font-size: 14px;
    border: 1px solid transparent;
    font-weight: 400;
}
.DayPicker-Day:hover {
    border-radius: 100px;
    background: #dad8d8;
}

.DayPicker-WeekNumber {
    display: table-cell;
    padding: .5rem;
    text-align: right;
    vertical-align: middle;
    min-width: 1rem;
    font-size: 0.75em;
    cursor: pointer;
    color: #8b9898;
}

.DayPicker--interactionDisabled .DayPicker-Day {
    cursor: default;
}

.DayPicker-Footer {
    display: table-caption;
    caption-side: bottom;
    padding: 5px 0 8px;
}

.DayPicker-TodayButton {
    display: none;
    border: none;
    background: transparent none;
    box-shadow: none;
    cursor: pointer;
    color: grey;
    font-size: 1em;
    text-decoration: underline;
}

/* Default modifiers */

.DayPicker-Day--today {
    /*color: #d0021b;*/
    font-weight: 600;
    border-radius: 100%;
    border: 1px solid #7CB8B0;
}

.beforeDisabledDays-enabled .DayPicker-Day--disabled {
    color: rgba(0,0,0,0.87);
    opacity: 0.5;
    cursor: default;
}

.beforeDisabledDays-enabled .DayPicker-Day--disabled:hover {
    background: none;
}

.DayPicker-Day--outside {
    color: rgba(0,0,0,0.50);
    height: 28px;
    padding: 0px;
}

/* Example modifiers */

.DayPicker-Day--sunday {
    background-color: #f7f8f8;
}

.DayPicker-Day--sunday:not(.DayPicker-Day--today) {
    color: #dce0e0;
}

.DayPicker-Day--selected:not(.DayPicker-Day--disabled):not(.DayPicker-Day--outside) {
    border-radius: 100%;
    outline: none;
    background-color: #7CB8B0;
    color: #fff;
    height: 26px;
}

